import React, { useState } from 'react';
import axios from 'axios'; 
import loginImage from './images/login-image.png'; 
import socialImage1 from './images/social1.svg'; 
import socialImage2 from './images/social2.svg'; 
import socialImage3 from './images/social3.svg';
import placeholderImage from './images/placeholder-image.svg'; 
import '../styles/login.css';
import { useAuth } from '../components/AuthProvider';
import { useNavigate } from 'react-router-dom'; //  useNavigate

const Login: React.FC = () => {  
  const { login } = useAuth();
  const navigate = useNavigate(); //   navigate
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [isFormValid, setIsFormValid] = useState(false);

  const handleUsernameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUsername(e.target.value);
    setIsFormValid(e.target.value.trim() !== '' && password.trim() !== '');
  };

  const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
    setIsFormValid(username.trim() !== '' && e.target.value.trim() !== '');
  };

  const handleLogin = async () => {
    setLoading(true);
    setError('');

    if (!username || !password) {
      setError(',     .');
      setLoading(false);
      return;
    }

    try {
      const response = await axios.post('https://gateway.scan-interfax.ru/api/v1/account/login', {
        login: username,
        password,
      }, {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
      });

      console.log('Full response:', response);

      const { accessToken, expire } = response.data;

      if (accessToken && expire) {
        localStorage.setItem('accessToken', accessToken);
        localStorage.setItem('tokenExpire', expire);
        console.log('Access token and expiration saved:', { accessToken, expire });
        login(accessToken);
        
        //   
        setUsername('');
        setPassword('');
        
        //    
        navigate('/'); //  '/'      
      } else {
        console.error('Access token or expiration date is missing in the response');
      }

    } catch (err) {
      if (axios.isAxiosError(err)) {
        console.error('Axios error:', err.response);
        setError(err.response?.data?.message || ' ');
      } else {
        console.error('Unexpected error:', err);
        setError(' ');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className='login__container'>
      <section className="subscription">
        <h1 className="subscription__title">    ,  .</h1>
        <div className="subscription__content">
          <img className="subscription__image" src={loginImage} alt=" " />
          <div className="subscription__placeholder">
            <img className="placeholder__image" src={placeholderImage} alt="" />
            <div className="auth__container">
              <div className="auth__item auth__item--login">
                <button className="auth__button auth__button--login active"></button>
                <div className="auth__underline auth__underline--login active"></div>
              </div>
              <div className="auth__item auth__item--register">
                <button className="auth__button auth__button--register"></button>
                <div className="auth__underline auth__underline--register"></div>
              </div>
            </div>

            {/*   */}
            <div className="subscription__form">
              <label className="subscription__label">   :</label>
              <input 
                type="text" 
                placeholder="   " 
                className="subscription__input" 
                value={username}
                onChange={handleUsernameChange} 
              />
              <label className="subscription__label">:</label>
              <input 
                type="password" 
                placeholder="" 
                className="subscription__input" 
                value={password}
                onChange={handlePasswordChange} 
              />
              {error && <p className="error-message">{error}</p>}
              <button 
                className={`subscription__button ${isFormValid ? 'active' : ''}`} 
                onClick={handleLogin}
                disabled={loading || !isFormValid} //  ,    
              >
                {loading ? '...' : ''}
              </button>
              <a href='#' className='subscription__href'> </a>
              <div className="social-login" style={{ width: '308px', height: '65px', marginTop: '20px' }}>
                <p className="social-login__text"> :</p>
                <div className="social-login__icons">
                  <img src={socialImage1} alt="  1" className="social-icon" />
                  <img src={socialImage2} alt="  2" className="social-icon" />
                  <img src={socialImage3} alt="  3" className="social-icon" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Login;
