import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import headerLogo from './images/header-logo.svg';
import avatarSvg from './images/avatar.png';
import BurgerSvg from './images/burger.svg';
import CloseSvg from './images/burgerclose.svg';
import { useAuth } from './AuthProvider';

const Header: React.FC = () => {
    const { logout, isAuthenticated, accountInfo, loading } = useAuth(); 
    const [menuOpen, setMenuOpen] = useState(false);

    const toggleMenu = () => {
        setMenuOpen(prev => !prev);
    };

    useEffect(() => {
        const token = localStorage.getItem('accessToken');
        console.log('Token:', token);
        if (token) {
            console.log('User  is authenticated');
        } else {
            console.log('User  is not authenticated');
        }
    }, []);

    const handleLogout = () => {
        logout();
    };

    return (
        <header className='header'>
            <img src={headerLogo} alt="logo" className="header__logo" />
            <nav className='header__nav'>
                <Link to="/"></Link>
                <Link to="/"></Link>
                <Link to="/">FAQ</Link>
            </nav>
            {isAuthenticated ? (
                <div className='header__user'>
                    <div className='header__account-info'>
                        {loading ? ( //  
                            <div className="loader"></div>
                        ) : (
                            accountInfo && (
                                <>
                                    <div>  <span className='accountInfo__used-companies'>{accountInfo.usedCompanies}</span></div>
                                    <div>   <span className='accountInfo__company-limit'>{accountInfo.companyLimit}</span></div>
                                </>
                            )
                        )}
                    </div>
                    <div className='header__login-menu'>
                        <span className='burger__span' onClick={toggleMenu} role="button" tabIndex={0} aria-label="Toggle menu">
                            <img src={BurgerSvg} alt='menu' className='button__burger-menu' />
                        </span>
                        <div className='login-menu__logout-block'>
                            <span> .</span> 
                            <button className='LogoutButton' onClick={handleLogout}></button>
                        </div>
                        <span className='user-avatar__span'>
                            <img src={avatarSvg} alt='Avatar' className='user-avatar' />
                        </span>                        
                    </div>
                </div>
            ) : (
                <div className='header__auth'>
                    <span className='burger__span' onClick={toggleMenu} role="button" tabIndex={0} aria-label="Toggle menu">
                        <img src={BurgerSvg} alt='menu' className='button__burger-menu' />
                    </span>
                    <span className='header__auth-visibleblock'>
                        <Link to="/register"></Link>
                        <div className='divider'>.</div>
                        <Link className='login-button' to="/login"></Link>
                    </span>
                </div>
            )}
            {menuOpen && (
                <div className='burger-menu'>
                    <span onClick={toggleMenu} className='close-button' aria-label="Close menu">
                        <img src={CloseSvg} alt='close' />
                    </span>
                    <div className='burger-content__block'>
                        <nav className='header__nav'>
                            <Link to="/"></Link>
                            <Link to="/"></Link>
                            <Link to="/">FAQ</Link>
                        </nav>
                        {isAuthenticated ? (
                            <div className='header__user'>                    
                                <div className='header__login-menu'>
                                    <div className='login-menu__logout-block'>
                                        <span> .</span> 
                                        <button className='LogoutButton' onClick={handleLogout}></button>
                                    </div>
                                    <span className='user-avatar__span'>
                                        <img src={avatarSvg} alt='Avatar' className='user-avatar' />
                                    </span>                        
                                </div>
                            </div>
                        ) : (
                            <div className='header__auth'>                 
                                <span className='header__auth-visibleblock'>
                                    <Link className='register-button' to="/register"></Link>
                                    <div className='divider'>.</div>
                                    <Link className='login-button' to="/login"></Link>
                                </span>
                            </div>
                        )}
                    </div>
                </div>
            )}
        </header>
    );
};

export default Header;
