//   
const initialState = {
    currentIndex: 0,
};

//  
const carouselReducer = (state = initialState, action: any) => {
    switch (action.type) {
        case 'NEXT':
            return {
                ...state,
                currentIndex: (state.currentIndex + 1) % 5, // 5 -  
            };
        case 'PREV':
            return {
                ...state,
                currentIndex: (state.currentIndex - 1 + 5) % 5, // 5 -  
            };
        default:
            return state;
    }
};

export default carouselReducer;