import React from 'react';
import { useSelector } from 'react-redux';
import { useAuth } from '../components/AuthProvider';
import '../styles/home.css';
import home1Image from './images/home1.png';
import home2Image from './images/home2.png';
import Carousel from '../components/Carousel'; 
import RateSection from '../components/RateSection'; 
import { Link } from 'react-router-dom';
const Home: React.FC = () => {
  const { isAuthenticated } = useAuth(); 
 
  return (
    <div className='home__container'>
      <h1 className='home__title '>  <br></br>  <br></br>
  <br></br>
   <img className="home__image1" src={home1Image} alt="  " />     </h1>
<p className='home__text'>  ,   <br></br>  PDF   .</p>         
            {isAuthenticated && (
                 <Link to="/search" >
    <button className="button__search">
       
    </button>
  </Link>
            )}
    <h2 className='home_titleh2 home_titleh2--carousel'>  </h2>
            <Carousel />
            <img className="home__image2" src={home2Image} alt="  " /> 
    <h2 className='home_titleh2'> </h2>
     <RateSection />
    </div>
  );
};

export default Home;
